import AppEventManager from '../Event/AppEventManager';
import AppEvent from '../Event/AppEvent';
import AppEventData from '../Event/AppEventData';
var UserListTemplate = /** @class */ (function () {
    function UserListTemplate(user) {
        this._user = user;
    }
    UserListTemplate.prototype.buildHtmlElement = function () {
        var html = document.createElement('div');
        html.innerHTML =
            "\n\t\t\t\t".concat(this._user.id(), "\n\t\t\t\t").concat(this._user.name(), "\n\t\t\t\t").concat(this._user.email(), "\n\t\t\t\t").concat(this._user.address(), "\n\t\t\t\t<input type=\"checkbox\" name=\"userDelete\" value=\"").concat(this._user.id(), "\">\n\t\t\t");
        html.appendChild(this._buildLinkEdit());
        return html;
    };
    UserListTemplate.prototype._buildLinkEdit = function () {
        var link = document.createElement('a');
        link.innerHTML = 'Edit';
        link.setAttribute('href', "/");
        link.addEventListener('click', function (event) {
            event.preventDefault();
            AppEventManager.trigger(new AppEvent('changePanel', new AppEventData('EditUser')));
        });
        return link;
    };
    return UserListTemplate;
}());
export default UserListTemplate;
